* {
    box-sizing: border-box;
}

body {
    font-family: sans-serif;
    margin: 3rem;
    background-color: #dfdfdf;
}

h1 {
    font-size: 3rem;
    font-weight: bold;
    text-align: center;
}

h1,
h2 {
    color: #333333;
    display: flex;
    align-items: center;
    margin-right: auto;
    margin-left: 10px;
}

p {
    font-size: 2rem;
    font-weight: bold;
}

.btn {
    font: inherit;
    padding: 1.65rem 2rem;
    cursor: pointer;
    border-radius: 4px;
    background-color: #80001c;
    color: white;
    border: 1px solid #80001c;
    font-size: 1.3rem;
    
}

.btn:hover {
    background-color: #a80024;
    border-color: #a80024;
}


.btn2 {
    font: inherit;
    padding: 1rem 2rem;
    cursor: pointer;
    border-radius: 4px;
    background-color: #80001c;
    color: white;
    border: 1px solid #80001c;
    font-size: 1.3rem;
    margin: 0.25rem;
}

.btn2:hover {
    background-color: #a80024;
    border-color: #a80024;
}

.btn--alt {
    background-color: transparent;
    color: #800040;
}

.btn--alt:hover {
    background-color: #f8dae9;
}

.card {
    background-color: white;
    border-radius: 4px;
    box-shadow: 0 1px 4px rgba(0, 0, 0, 0.2);
    width: 100%;
    height: 5rem;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin: 0.5rem;
}

.actions {
    text-align: right;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
}

.modal {
    box-shadow: 0 1px 4px rgba(0, 0, 0, 0.2);
    border-radius: 6px;
    background-color: white;
    padding: 1rem;
    text-align: center;
    width: 30rem;
    z-index: 10;
    position: fixed;
    top: 20vh;
    left: calc(50% - 15rem);
}

.backdrop {
    position: fixed;
    z-index: 1;
    background-color: rgba(0, 0, 0, 0.75);
    width: 100%;
    height: 100vh;
    top: 0;
    left: 0;
}

.checkbox {
    display: inline-block;
    width: 2rem;
    height: 2rem;
    border: 1px solid #ccc;
    border-radius: 4px;
    margin: 1rem;
    box-shadow: 0 1px 4px rgba(0, 0, 0, 0.2);
    background-color: #fff;
}

.input--text {
    width: 100%;
    height: 5rem;
    padding: 1rem;
    border: 1px solid #ccc;
    border-top-left-radius: 4px;
    border-bottom-left-radius: 4px;
    margin-right: 0;
    box-shadow: 0 1px 4px rgba(0, 0, 0, 0.2);
    font-size: 1.5rem;
    font-weight: bold;
}

.input--text:focus {
    outline: none;
    border-color: #00fc00;
}

.btn-add {
    background-color: #0f8000;
    border-color: #0f8000;
    color: white;
    font-size: 3rem;
    font-weight: bold;
    padding: 0.5rem 1.5rem;
    border-top-right-radius: 4px;
    border-bottom-right-radius: 4px;
    display: inline;
    margin-left: 0;
    margin-right: 0;
    cursor: pointer;
}

.btn-add:hover {
    background-color: #13a300;
    border-color: #13a300;
}

.form-flex {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 100%;
    margin: 0.5rem;
    margin-top: 3rem;
}

.cross-out {
    text-decoration: line-through;
}